import React, {useState,useEffect} from 'react';
import {connect} from "react-redux";
import * as actions from "../actions/income";
import Report from './Report/Report';
import Form1 from './Form1';
import { faEdit } from "@fortawesome/free-solid-svg-icons";
import { faTrash } from "@fortawesome/free-solid-svg-icons";
//import { useToasts } from "react-toast-notifications";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";



//function Incomes(){
   

    const Incomes = ({classes,...props}) =>{

            const [currentId, setCurrentId] = useState(0)
        
            useEffect(() => {
                props.fetchAllIncomes()
            }, [])//componentDidMount
            
            //toast msg.
            //const { addToast } = useToasts()
        
            const onDelete = id => {
                if (window.confirm('Are you sure to delete this record?'))
                    props.deleteIncomes(id,()=>( { appearance: 'info' }))
            }
    return(
        <div class="acnav">
         
         
    <table>
    <tr>
        <th>Income ID</th>
        <th>Name</th>
        <th>Email</th>
        <th>Amount</th>
        <th>Date</th>
        <th></th>
    </tr>
    
      
    {
            props.incomeList.map((item, index) => {
                return(<tr key={index} hover>
                
                    <td>{item.id}</td>
                    <td>{item.fullName} </td>
                    <td>{item.email}</td>
                    <td>{item.amount}</td>
                    <td>{item.date}</td>
                    <td> 
                        <div align="right">


    <FontAwesomeIcon icon={faEdit} color="black" className="mr-2"  onClick={() => {setCurrentId(item.id) }}/>

    <a href="#">
    <FontAwesomeIcon
    icon={faTrash}
    color="black"
    className="mr-2"
    data-toggle="modal"
    data-target="#myModal3"/></a>

    <div class="modal fade" id="myModal3" role="dialog">
    <div class="modal-dialog">
    <div class="modal-content">
    <div class="modal-header">
    <h4 className="modal-title">Are you sure?</h4>
    
  </div>
 
  <div class="modal-footer">
    <button
      type="button"
      class="btn btn-default"
      data-dismiss="modal">
      Close
    </button>
    <button type="button" class="btn btn-danger" onClick={()=> onDelete(item.id)}  variant="danger" >
      Delete
    </button>
  </div>
</div>
</div>
</div>
</div></td>
                </tr>)

                })
}


    </table>
   
    <Form1 {...({ currentId, setCurrentId })} />
    
 
    </div>
       


        
    );
}




const mapStateToProps = state=>({
    
        incomeList: state.income.list
    
})

const mapActionToProps={
    fetchAllIncomes : actions.fetchAll,
    deleteIncomes:actions.Delete
}
export default connect(mapStateToProps, mapActionToProps)(Incomes);
