import React, { useEffect, useState } from "react";
import generatePDF from './ReportGenerator';
import IncomesComponent from "./IncomeReportComponent";
import { axios } from "axios";

const Incomes = () => {
    const[income,setIncome]=useState([]);
  useEffect(() => {
    const getAllIncomes = async () => {
      try {
        fetch("http://localhost:63286/api/Incomes", {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
          },
        })
          .then((res) => res.json())
          .then(
            (result) => {
              setIncome(result);
            },
            (error) => {
              this.setState({
                isLoaded: true,
                error,
              });
            }
          );
      } catch (err) {
        console.log("error");
      }
    };
    getAllIncomes();
  }, []);

  //   const reportTokens = tokens.filter((token) => token.status === "0");

  return (
    <div  class="acnav" align="center">
      <div className="container mb-5 mt-4 p-3">
        <h3>Monthly Income sales Report</h3>
      </div>
      <IncomesComponent income={income} />
      <button className="btn btn-outline-success m-5" align="center" onClick={() => generatePDF(income)}>
        Generate monthly report
      </button>
    </div>
  );
};

export default Incomes;