import React from "react"
import "./ImageSlider.css";
import img4 from './img4.jpg';
import image6 from './image6.jpg';
import image5 from './image5.jpg';
import img7 from './img7.jpg';

function ImageSlider() {
 return (
  <div className = "acnav ">
     
  <div id="carouselExampleIndicators" class="carousel slide sld1 " data-ride="carousel">
   <ol class="carousel-indicators">
    <li data-target="#carouselExampleIndicators" data-slide-to="0" class="active"></li>
    <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
    <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
    <li data-target="#carouselExampleIndicators" data-slide-to="3"></li>
    <li data-target="#carouselExampleIndicators" data-slide-to="4"></li>
   </ol>
   <div class="carousel-inner ">
    <div class="carousel-item active">
    <img class="d-block w-100" src={img7} alt="First slide" />
    </div>
    <div class="carousel-item">
     <img class="d-block w-100" src={image6} alt="Second slide" />
    </div>
    <div class="carousel-item">
     <img class="d-block w-100" src={img4} alt="Third slide" />
    </div>
    <div class="carousel-item">
     <img class="d-block w-100" src={img7} alt="fourth slide" />
    </div>
    <div class="carousel-item">
     <img class="d-block w-100" src={image5} alt="fourth slide" />
    </div>
   </div>
   <a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
    <span class="sr-only">Previous</span>
   </a>
   <a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
    <span class="carousel-control-next-icon" aria-hidden="true"></span>
    <span class="sr-only">Next</span>
   </a>
  </div>
  </div>
 );
}

export default ImageSlider;