import React, { useState, useEffect } from "react";
import "./Financialpages/Form.css";
import * as actions from "../actions/income";
import {connect} from "react-redux";
import useForm from "./useForm";
//import { useToasts } from "react-toast-notifications";





const initialFieldValues={
    id : '',
    fullName : '',
    email : '',
    amount : '',
    date : ''
}
const Form = ({ classes, ...props}) => {
//const Form = (props) =>{
    //const { addToast } = useToasts()
 //validate()
    const validate = (fieldValues = values) => {
        let temp = { ...errors }
       
        if ('fullName' in fieldValues)
            temp.fullName = fieldValues.fullName ? "" : "This field is required."
        
        if ('amount' in fieldValues)
            temp.amount = fieldValues.amount? "" : "This field is required."
        if ('email' in fieldValues)
            temp.email = (/^$|.+@.+..+/).test(fieldValues.email) ? "" : "Email is not valid."
        setErrors({
            ...temp
        })

        if (fieldValues == values)
            return Object.values(temp).every(x => x == "")
    }
    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    } = useForm(initialFieldValues, validate, props.setCurrentId)

    const handleSubmit = e => {
       
        e.preventDefault()
        console.log(values)
        /*if (validate()) {
        const onSuccess = () => {
            alert("validated")
            resetForm()
             ( { appearance: 'success' })
        }

        alert(props.currentId)
        if (props.currentId == 0)
            props.createIncomes(values, onSuccess)
        else
            props.updateIncomes(props.currentId, values, onSuccess)

        }*/
    }

    //sumbit
    /*const submitNew = (e) => {
        e.preventDefault();
        fetch("http://localhost:63286/api/Incomes", {
          method: "post",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            id: Number(this.state.id),
            name: this.state.name,
            email: this.state.email,
            amount: this.state.amount,
            date: this.state.date,
          }),
        })
          .then((res) => res.json())
          .catch((err) => console.log(err));
      };*/


    useEffect(() => {
        if (props.currentId != 0) {
            setValues({
                ...props.incomeList.find(x => x.id == props.currentId)
            })
            setErrors({})
        }
    }, [props.currentId])



    return ( 
        <div class="acnav">   
        <div> 
                <form className="divs" autoComplete="off" noValidate onSubmit={handleSubmit} >
                <fieldset class="fsf">
        <div class="card">
        
        
                <div class="form-group" className="container mt-4"  >
                
                    
                    <label ><b>Name</b></label><br></br>
                    <input type="text"class="autocomplete" 
                     name="fullName"
                     
                     variant="outlined"
                     value={values.fullName} 
                     onChange={handleInputChange} id="name" placeholder="Enter payment person Name"
                     helperText={errors.fullName}
                     {...(errors.fullName && { error: true, helperText: errors.fullName })}></input><br></br>
        
                    
                    <label for="autocomlete-input" ><b>Email</b></label><br></br>
                    <input  type="email" class="autocomplete"
                     name="email"
                     variant="outlined"
                     value={values.email} 
                     onChange={handleInputChange} id="autocomplete-input" placeholder="Enter email"
                     {...(errors.email && { error: true, helperText: errors.email })}></input><br></br>
                    
        
                    <label for="autocomlete-input"><b>Date</b></label><br></br>
                    <input type="date" class="autocomplete" 
                     name="date"
                     variant="outlined"
                     value={values.date} 
                     onChange={handleInputChange} id="date"
                     {...(errors.date && { error: true, helperText: errors.date })}></input><br></br>
        
                    
                    <label ><b>Amount</b></label><br></br>
                    <input type="text" class="autocomplete" 
                     name="amount"
                     variant="outlined"
                     value={values.amount} 
                     onChange={handleInputChange} id="amount" placeholder="Enter amount"
                     {...(errors.amount && { error: true, helperText: errors.amount })}></input><br></br><br></br>
                   <button className="btn btn-outline-success m-3" 
                                  variant="contained"
                                  color="primary"
                                  type="submit"
                                  //onClick={submitNew}
                                     >
                                        Submit</button>
                    <button className="btn btn-outline-success m-3" 
                                    variant="contained"
                                    onClick={resetForm}
                                >
                                    Reset
                                </button> 
                
                </div>
          
                  
        </div>
        
        </fieldset>
        </form>
        </div>
        </div>
        
        
        
        );
    }

    
const mapStateToProps = state=>({
    
    incomeList: state.income.list

})

const mapActionToProps={
    createIncomes: actions.create,
    updateIncomes: actions.update
}
export default connect(mapStateToProps, mapActionToProps)(Form);