import React from 'react';
import {store} from './actions/store';
import { Provider } from "react-redux";
import Navbar from './components/Sidebar/Navbar';
import {BrowserRouter, Route, Switch} from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import Financialdashboard from './components/Financialpages/Financialdashboard';
import HeaderBar from './components/HeaderFooter/HeaderBar';
import Expensespage from './components/Financialpages/Expensespage';
import Footer from './components/HeaderFooter/Footer';
import Form1 from './components/Form1';
import IncomeReport from './components/Report/IncomeReport';
import ImageSlider from './components/ImageSlider';
import Currency from './components/Financialpages/Currency';
import Incomes from './components/Incomes';





function App() {
  return (
    <Provider store={store}>
      
   
    <div > 
   
   <BrowserRouter>
  
   <HeaderBar/>
   <Navbar/>
 
  
   
  
   <Switch> 
     <Route path="/report">
     <Financialdashboard/>
     <IncomeReport/>
   
     </Route>
     
     <Route path ="/income">
     <Financialdashboard/>
     <Incomes/>
     </Route>

     <Route path ="/expenses" >
     <Financialdashboard/>
     <Expensespage/>
     </Route>

     <Route path ="/Form" >
     <Financialdashboard/>
     <Form1/>
     </Route>

     <Route path ="/" >
     <Financialdashboard/>
     <ImageSlider/>
     <Currency/>
     </Route>
   
     </Switch>

     <Footer/>
    
   </BrowserRouter>
   
   
    </div>

     </Provider>
  );
} 
export default App;
